import React, { useState, useEffect } from "react";
import BlogForm from "./components/BlogForm";
import BlogPosts from "./components/BlogPosts";
import Notification from "./components/Notification";
import FilterComponent from "./components/FilterComponent";
import blogpostsServices from "./services/blogposts";
import "./App.css";

function App() {
  const [blogPosts, setBlogPosts] = useState([]);
  const [newAuthor, setNewAuthor] = useState("");
  const [newTitle, setNewTitle] = useState("");
  const [newUrl, setNewUrl] = useState("");
  const [likes, setLikes] = useState(0);
  const [newNotification, setNotification] = useState(null);
  const [newFilter, setNewFilter] = useState("");
  const [filterCategory, setFilterCategory] = useState("author");

  useEffect(() => {
    blogpostsServices
      .getAll()
      .then((data) => setBlogPosts(data))
      .catch((error) => console.log("there was an error", error));
  }, []);

  const createRandomId = () => {
    return Math.floor((1 + Math.random()) * 0x100);
  };

  const resetInputFields = () => {
    setNewAuthor("");
    setNewTitle("");
    setNewUrl("");
  };

  const resetAllInputs = (event) => {
    console.log("reset is pressed");
    event.preventDefault();
    resetInputFields();
  };

  const displayMessage = (message, type) => {
    setNotification({ message, type });
    setTimeout(() => {
      setNotification(null);
    }, 2000);
  };

  const updatePost = (oldPost, newPost) => {
    const answer = window.confirm(
      `${oldPost.title} already exists. Do you want to update this entry?`
    );
    if (answer) {
      blogpostsServices.update(oldPost.id, newPost).then((response) => {
        setBlogPosts(
          blogPosts.map((p) => (p.id !== oldPost.id ? p : response))
        );
        resetInputFields();
        displayMessage(`Entry for ${newPost.title} was changed`, "success");
      });
    } else {
      resetInputFields();
    }
  };

  const addBlogPost = (event) => {
    event.preventDefault();
    const newBlogPost = {
      author: newAuthor,
      title: newTitle,
      url: newUrl,
      likes: likes,
      id: createRandomId(),
    };

    const equalPost = blogPosts.filter(
      (p) => p.title.toLowerCase() === newBlogPost.title.toLowerCase()
    );

    equalPost.length > 0
      ? updatePost(equalPost[0], newBlogPost)
      : blogpostsServices
          .create(newBlogPost)
          .then((returnedBlogPost) => {
            setBlogPosts(blogPosts.concat(returnedBlogPost));
            resetInputFields();
            displayMessage(
              `${returnedBlogPost.title} has been added`,
              "success"
            );
          })
          .catch((error) => {
            resetInputFields();
            console.log("this is the error", error);
          });
  };

  const onLikeClick = (id) => {
    const postToUpdate = blogPosts.find((p) => p.id === id);
    const updatedPost = {
      likes: setLikes(postToUpdate.likes++),
      ...postToUpdate,
    };

    blogpostsServices
      .update(id, updatedPost)
      .then((data) =>
        setBlogPosts(blogPosts.map((p) => (p.id === id ? data : p)))
      );
  };

  const onDeleteClick = (id, title) => {
    const answer = window.confirm(`Delete ${title}?`);
    if (answer) {
      blogpostsServices
        .deletePost(id)
        .then((data) => {
          setBlogPosts(blogPosts.filter((p) => p.id !== id));
          displayMessage(`${title} has been deleted`, "success");
        })
        .catch((error) =>
          displayMessage(`${title} has already been deleted`, "fail")
        );
    } else {
      setBlogPosts(blogPosts);
    }
  };

  const handleAuthorChange = (event) => {
    setNewAuthor(event.target.value);
  };

  const handleTitleChange = (event) => {
    setNewTitle(event.target.value);
  };

  const handleUrlChange = (event) => {
    setNewUrl(event.target.value);
  };

  const handleCategoryChange = (event) => setFilterCategory(event.target.value);

  const handleFilterChange = (event) => {
    setNewFilter(event.target.value);
  };

  const filterByCategory = (type, list, filter) => {
    if (type === "author") {
      return list.filter((p) =>
        p.author.toLowerCase().includes(filter.toLowerCase())
      );
    } else if (type === "title") {
      return list.filter((p) =>
        p.title.toLowerCase().includes(filter.toLowerCase())
      );
    } else if (type === "likes") {
      const integer = parseInt(filter);
      if (integer) {
        return list.filter((p) => p.likes >= integer);
      } else {
        console.log("please enter a number");
      }
    }
  };

  const postsToShow =
    newFilter !== ""
      ? filterByCategory(filterCategory, blogPosts, newFilter)
      : blogPosts;

  return (
    <div className="App">
      <h1>Blog List</h1>
      <Notification notificationObject={newNotification} />
      <FilterComponent
        category={filterCategory}
        handleCategoryChange={handleCategoryChange}
        newFilter={newFilter}
        onFilterInputChange={handleFilterChange}
      />
      <h2>Add a new blog entry</h2>
      <BlogForm
        onSubmitChange={addBlogPost}
        onClear={resetAllInputs}
        onAuthorChange={handleAuthorChange}
        onTitleChange={handleTitleChange}
        onUrlChange={handleUrlChange}
        authorValue={newAuthor}
        titleValue={newTitle}
        urlValue={newUrl}
      />
      <h2>List of blogs</h2>
      <BlogPosts
        list={postsToShow}
        onLikeClick={onLikeClick}
        onDeleteClick={onDeleteClick}
      />
    </div>
  );
}

export default App;
