import React from "react";

const Post = ({
  author,
  title,
  url,
  likes,
  id,
  onLikeClick,
  onDeleteClick,
}) => {
  return (
    <tr>
      <td>{author}</td>
      <td>{title}</td>
      <td>
        <a href={url}>Link to {title}</a>
      </td>
      <td>{likes}</td>
      <td>
        <button onClick={() => onLikeClick(id)}>LIKE</button>
      </td>
      <td>
        <button onClick={() => onDeleteClick(id, title)}>DELETE</button>
      </td>
    </tr>
  );
};

export default Post;
