import React from "react";
import Filter from "./Filter";

const FilterComponent = ({
  category,
  handleCategoryChange,
  newFilter,
  onFilterInputChange,
}) => (
  <div>
    <form>
      <label>
        <input
          type="radio"
          name="categorie"
          value="author"
          checked={category === "author"}
          onChange={handleCategoryChange}
        />
        author
      </label>
      <label>
        <input
          type="radio"
          name="categorie"
          value="title"
          checked={category === "title"}
          onChange={handleCategoryChange}
        />
        title
      </label>
      <label>
        <input
          type="radio"
          name="categorie"
          value="likes"
          checked={category === "likes"}
          onChange={handleCategoryChange}
        />
        likes
      </label>
    </form>
    <Filter
      type={category}
      newFilter={newFilter}
      onChange={onFilterInputChange}
    />
  </div>
);

export default FilterComponent;
