import React from "react";

const Notification = ({ notificationObject }) => {
  const messageStyle = {
    fontSize: "20px",
    borderStyle: "solid",
    borderRadius: "5px",
    padding: "10px",
    marginBottom: "10px",
  };

  const success = {
    color: "#429745",
    background: "#a9daab",
    ...messageStyle,
  };

  const fail = {
    color: "#a34847",
    background: "#d49e9d",
    ...messageStyle,
  };

  if (notificationObject === null) {
    return null;
  }
  return (
    <div style={notificationObject.type === "success" ? success : fail}>
      {notificationObject.message}
    </div>
  );
};

export default Notification;
