import React from "react";
import Post from "./Post";

const BlogPosts = (props) => {
  return (
    <table>
      <thead>
        <tr>
          <th>Author</th>
          <th>Title</th>
          <th>Url</th>
          <th>Likes</th>
          <th>Like Button</th>
          <th>Delete Button</th>
        </tr>
      </thead>
      <tbody>
        {props.list.map((p) => (
          <Post
            key={p.id}
            author={p.author}
            title={p.title}
            url={p.url}
            likes={p.likes}
            id={p.id}
            onLikeClick={props.onLikeClick}
            onDeleteClick={props.onDeleteClick}
          />
        ))}
      </tbody>
    </table>
  );
};

export default BlogPosts;
