import React from "react";

const BlogForm = (props) => (
  <form onSubmit={props.onSubmitChange}>
    <p>
      author:
      <input value={props.authorValue} onChange={props.onAuthorChange} />
    </p>
    <p>
      title: <input value={props.titleValue} onChange={props.onTitleChange} />
    </p>
    <p>
      url: <input value={props.urlValue} onChange={props.onUrlChange} />
    </p>
    <p>
      <button type="submit">ADD</button>
      <button onClick={props.onClear} type="reset">
        CLEAR
      </button>
    </p>
  </form>
);

export default BlogForm;
